/* ØªØ·Ø¨ÙŠÙ‚ box-sizing: border-box Ø¹Ù„Ù‰ Ø¬Ù…ÙŠØ¹ Ø§Ù„ÙƒÙ„Ø§Ø³Ø³ */
* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 70px 0 0 0;
  height: 100%;
  font-family: "Arial", sans-serif;
}
.m-d-hamza {
  margin-top: 100px;
}

.main-container {
  display: flex;
  flex-direction: row;
  gap: 20px;
  padding: 0;
  min-height: 100vh;
  width: 100%;
  background-color: #ffffff;
  align-items: stretch; /* Ù„Ø¶Ù…Ø§Ù† ØªÙ…Ø¯Ø¯ Ø§Ù„Ø¹Ù†Ø§ØµØ± Ø§Ù„Ø§Ø¨Ù†Ø§Ø¡ Ø¨Ø§Ù„ÙƒØ§Ù…Ù„ */
    
}

/* Ø§Ù„Ù‚Ø³Ù… Ø§Ù„Ø£ÙŠØ³Ø± (User Card) */
.user-card {
  background: #800020;
  padding: 40px;
  border-radius: 0;
  color: white;
  text-align: center;
  flex: 0 0 35%;
  /* ÙŠØ£Ø®Ø° 35% Ù…Ù† Ø§Ù„Ø¹Ø±Ø¶  Ø¨Ø§Ù„Ø´Ø§Ø´Ø§Øª Ø§Ù„ÙƒØ¨ÙŠØ±Ø© */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  
}

/* Ø§Ù„ØµÙˆØ±Ø© Ø¯Ø§Ø®Ù„ Ø§Ù„Ù‚Ø³Ù… Ø§Ù„Ø£ÙŠØ³Ø± */
.grp-6-pic {
  width: 180px;
  height: 180px;
  object-fit: cover;
  margin-bottom: 20px;
  transition: transform 0.3s ease;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.3);
}

.grp-6-pic:hover {
  transform: scale(1.03);
}

.grp-6-btn {
  border-radius: 17px;
  transition: all 0.3s ease;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
}

.grp-6-btn:hover {
  transform: scale(1.03);
}

/* Ø§Ù„Ù‚Ø³Ù… Ø§Ù„Ø£ÙŠÙ…Ù† (User Info & Favorites) */
.user-info {
  padding: 0px 40px 40px 40px;
  flex: 1; /* ÙŠØ£Ø®Ø° Ø¨Ø§Ù‚ÙŠ Ø§Ù„Ø¹Ø±Ø¶ */
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  /*  Ø¸Ù„ Ø¯Ø§Ø®Ù„ÙŠ */
  box-shadow: inset 0 0 50px rgba(0, 0, 0, 0.3);
  height: 100vh;
   background-color: #ddd3d3; 
  animation: backgroundChange 8s infinite;
}


.Profile-Information {
  margin: 30px !important;
}

/* ØªÙ†Ø³ÙŠÙ‚ Ø§Ù„Ø¹Ù†Ø§ØµØ± Ø¯Ø§Ø®Ù„ Ù‚Ø§Ø¦Ù…Ø© Ø§Ù„Ù…Ø¹Ù„ÙˆÙ…Ø§Øª */
.list-group-item .text-muted {
  font-size: 0.9rem;
  margin-bottom: 5px;
}

.list-group-item .btn-outline-success {
  border-radius: 5px;
}

.list-group-item .btn-outline-success:hover {
  background-color: #28a745;
  color: white;
}

.card-title {
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}
.card-body {
  padding: 15px;
}

.card .btn-primary:hover {
  background-color: #0056b3;
}

/* Ø´Ø§Ø´Ø§Øª Ø£Ù‚Ù„ Ù…Ù† 768px (Ù‡ÙˆØ§ØªÙ Ù…Ø­Ù…ÙˆÙ„Ø©) */
@media (max-width: 768px) {
  .main-container {
    flex-direction: column; /* ØªØ­ÙˆÙŠÙ„ Ø§Ù„Ø¹Ø±Ø¶ Ø¥Ù„Ù‰ Ø¹Ù…ÙˆØ¯ */
    gap: 0; /* Ø¥Ø²Ø§Ù„Ø© Ø§Ù„ÙØ±Ø§ØºØ§Øª */
    
  }

  .user-card {
    display: flex;

    padding: 30px;
  }

  .grp-6-pic {
    width: 200px;
    height: 200px;
  }
}

/* Ø´Ø§Ø´Ø§Øª Ø¨ÙŠÙ† Ù§Ù¦Ù©px Ùˆ Ù¡Ù Ù¢Ù¤px (Ø£Ø¬Ù‡Ø²Ø© Ù„ÙˆØ­ÙŠØ© ÙˆØ£Ø¬Ù‡Ø²Ø© Ù…ØªÙˆØ³Ø·Ø©) */
@media (min-width: 769px) and (max-width: 1024px) {
  .main-container {
    background: #800020;
    display: grid;
    grid-template-columns: 0.8fr 1fr;
  }

  .user-card {
    background: transparent; /* Ø¬Ø¹Ù„ Ø§Ù„Ø®Ù„ÙÙŠØ© Ø´ÙØ§ÙØ© */

    padding: 30px;
    position: sticky;
    top: 130px;
    align-self: flex-start;
  }

  .grp-6-pic {
    width: 220px;
    height: 220px;
  }
  .user-info {
    padding: 0px 40px 40px 40px;
    height: 100%;
  }
}

/* Ø´Ø§Ø´Ø§Øª ÙƒØ¨ÙŠØ±Ø© (Ø£ÙƒØ¨Ø± Ù…Ù† 1024px) */
@media (min-width: 1025px) {
  .main-container {
    background: #800020;
    display: grid;
    grid-template-columns: 0.5fr 1fr;
    gap: 0;
  }

  .grp-6-pic {
    width: 270px;
    height: 270px;
  }
  /* Ø§Ù„Ù‚Ø³Ù… Ø§Ù„Ø£ÙŠØ³Ø± (User Card) */
  .user-card {
    background: transparent; /* Ø¬Ø¹Ù„ Ø§Ù„Ø®Ù„ÙÙŠØ© Ø´ÙØ§ÙØ© */
    position: sticky;
    top: 120px;
    align-self: flex-start;
  }
}
