.hz-page-title {
  text-align: center;
  color: #fff;
  font-size: 2rem;
  margin-bottom: 2rem;
}

.hz-event-cards-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
}


@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
