.marquee {
    position: relative;
    overflow: hidden;
    width: 100%;
    top:16px;
    bottom: 45px;
    height: 50px; 
  }
  
  .marquee-content {
    display: flex;
    animation: marquee-scroll 25s linear infinite; 
  }
  
